* {
    margin: 0;
    padding: 0; }


*,
*::before,
*::after {
    box-sizing: inherit; }

html,
body {
    box-sizing: border-box;
    margin: 0px;
    width: 100vw;
    font-family: laca, sans-serif; }

ul {
    list-style: none; }

img {
    width: 100%;
    height: 100%;
    object-fit: cover; }

input,
textarea {
    background: transparent;
    border: none;
    outline: none; }

button {
    background: none;
    border: none;
    outline: none; }
