.game__content {
    z-index: 100;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    .content__game {
        padding: 2%;
        z-index: 100;
        overflow: hidden;
        margin-top: 50px;
        width: $sr-width * 2.6px;
        height:  $sr-height * 2.6px;
        background-color: white;
        border-radius: $br-main;
        border: 2px solid $second-color;
        display: flex;
        justify-content: center;
        align-items: center; } }


/* For mobile landscape */
@media only screen and (min-width: 600px) {
    .game__content {
        width: 100%;
        height: 100vh;
        .content__game {
            margin-top: 20px;
            width: $sr-width * 4px;
            height:  $sr-height * 4px; } } }


@media only screen and (min-width: 768px) {
    /* For tablet landscape */
    .game__content {
        width: 100%;
        height: 100vh;
        .content__game {
            margin-top: 20px;
            width: $sr-width * 4px;
            height:  $sr-height * 4px; } } }


@media only screen and (min-width: 1024px) {
    /* For desktop or big tablet */
    .game__content {
        width: 100%;
        height: 100vh;
        .content__game {
            margin-top: 60px;
            width: $sr-width * 8px;
            height:  $sr-height * 8px; } } }


@media only screen and (min-width: 1920px) {
    /* For desktop or big tablet */
    .game__content {
        width: 100%;
        height: 100vh;
        .content__game {
            margin-top: 80px;
            width: $sr-width * 8px;
            height:  $sr-height * 8px; } } }
