.tours__content {
    z-index: 9998;
    margin: 0px auto;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    width: 90%;
    height: 100vh;
    padding: 4% 0;

    .content__tour__menu {
        @include on-circle(8, 70%, 200);
        margin-top: 200px;
        img {
            display: block;
            max-width: 100%;
            transition: .15s; } }
    .menu__item--icon {
        position: absolute;
        top: -110%;
        left: 25%;
        width: 50%;
        img {
            max-width: 100%;
            max-height: 100%;
            display: block;
            transition: .15s; } } }

.content___tour__menu__item:hover {
    .menu__item--icon {
        transition: .15s;
        transform: translateY(-10px); } }



/* For mobile landscape */
@media only screen and (min-width: 600px) {
    .tours__content {
        width: 90%;
        height: 100vh;
        padding: 4% 0;

        .content__tour__menu {
            @include on-circle(8, 70%, 90);
            margin-top: 100px; }

        .menu__item--icon {
            position: absolute;
            top: -110%;
            left: 25%;
            width: 50%; } } }

