//GAME-CONTAINER
.memory-game-container {
    height: 100vh;
    display: flex; }

.memory-game {
    width: 640px;
    height: 640px;
    margin: auto;
    display: flex;
    flex-wrap: wrap;
    perspective: 1000px; }


.memory-card {
    width: 187px;
    height: 187px;
    margin: 5px;
    position: relative;
    transform: scale(1);
    transform-style: preserve-3d;
    transition: transform .5s; }


.memory-card:active {
    transform: scale(0.97);
    transition: transform .2s; }


.memory-card.flip {
    transform: rotateY(180deg); }

.front-face {
    width: 100%;
    height: 100%;
    padding: 20px;
    position: absolute;
    border-radius: 5px;
    background: $second-color;
    backface-visibility: hidden; }

.front-face {
    transform: rotateY(180deg); }


.back-face {
    height: 100%;
    width: 100%;
    border-radius: 5px;
    background-image: url('/svgs/games/memory/back.svg');
    background-repeat: no-repeat;
    background-size: 100%;
    background-position: bottom; }


.back-face:hover {
    cursor: pointer; }
