
.bunny {
    position: absolute;
    width: 8%;
    bottom: 1%;
    right: 2%;
    img {
        max-width: 100%;
        max-height: 100%;
        display: block; } }
.bunny__active {
    bottom: 5%;
    right: 4%;
    transition: .30s; }

@media only screen and (min-width: 600px) {
    /* For mobile landscape */
    .bunny {
        position: absolute;
        width: 8%;
        bottom: 1%;
        right: 2%;
        transition: .30s; }
    .bunny__active {
        bottom: 7%;
        right: 4%;
        transition: .30s; } }

@media only screen and (min-width: 768px) {
    /* For tablet landscape */
    .bunny {
        position: absolute;
        width: 8%;
        bottom: 1%;
        right: 2%;
        transition: .30s; }
    .bunny__active {
        bottom: 7%;
        right: 4%;
        transition: .30s; } }

@media only screen and (min-width: 1200px) {
    /* For desktop or big tablet */
    .bunny {
        position: absolute;
        width: 8%;
        bottom: 1%;
        right: 2%;
        transition: 0.5s;
        transition-timing-function: ease-in-out; }
    .bunny__active {
        bottom: 7%;
        right: 4%;
        transition: .30s; } }
