
/* For small Mobile */
.lucas__HI {
    z-index: 100;
    position: absolute;
    width: 10%;
    top: 56%;
    left: 45%;
    img {
        max-width: 100%;
        max-height: 100%;
        display: block; } }
.lucas__SICK {
    position: absolute;
    width: 10%;
    top: 56%;
    left: 45%;
    img {
        max-width: 100%;
        max-height: 100%;
        display: block; } }
.lucas__LOOK {
    z-index: 100;
    position: absolute;
    width: 12%;
    bottom: 0;
    left: 1%;
    transform: translateY(16%);
    .lucas__title {
        color: $second-color;
        position: absolute;
        bottom: 22%;
        right: 6%;
        max-width: 70%;
        width: 70%;
        text-align: center;
        display: flex;
        justify-content: center;
        .lucas__title--title {
            width: 100%; } }
    img {
        max-width: 100%;
        max-height: 100%;
        display: block; } }

.lucas__BYE {
    position: absolute;
    width: 10%;
    top: 56%;
    left: 45%;
    img {
        max-width: 100%;
        max-height: 100%;
        display: block; } }


@media only screen and (min-width: 600px) {
    /* For mobile landscape */
    .lucas__HI {
        position: absolute;
        width: 12%;
        top: 52%;
        left: 45%; }

    .lucas__LOOK {
        width: 12%;
        bottom: 0;
        left: 0%;
        .lucas__title {
            bottom: 22%; } } }

@media only screen and (min-width: 768px) {
    /* For tablet landscape */
    .lucas__HI {
        position: absolute;
        width: 12%;
        top: 52%;
        left: 45%; }
    .lucas__LOOK {
        width: 12%;
        bottom: 0;
        left: 2%;
        .lucas__title {
            bottom: 22%; } } }


@media only screen and (min-width: 1200px) {
    /* For desktop or big tablet */
    .lucas__HI {
        position: absolute;
        width: 10%;
        top: 57%;
        left: 42%; }
    .lucas__LOOK {
        width: 12%;
        bottom: 0;
        left: 2%;
        .lucas__title {
            bottom: 22%; } } }
