@mixin disableSiteForPortrait {
    display: none; }

/// Mixin to place items on a circle
/// @author Hugo Giraudel
/// @author Ana Tudor
/// @param {Integer} $item-count - Number of items on the circle
/// @param {Length} $circle-size - Large circle size
/// @param {Length} $item-size - Single item size
@mixin on-circle($item-count, $circle-size, $item-size) {
    position: relative;
    width:  #{$circle-size};
    height: #{$circle-size};
    padding: 0;
    border-radius: 50%;
    list-style: none;

    > * {
        display: block;
        position: absolute;
        top:  50%;
        left: 50%;
        width:  #{$item-size}px;

        margin:  #{-($item-size / 2)}px;

        $angle: (360 / $item-count);
        $rot: -(360 / $item-count);

        @for $i from 1 through $item-count {
            @if $i == 1 {
                &:nth-child(#{$i}) {
                    transform: rotate($rot * 1deg) translate(#{$circle-size + 10%},#{$circle-size + 100%}) rotate($rot * -1deg); } }
            @else if $i == 2 {
                &:nth-child(#{$i}) {
                    transform: rotate($rot * 1deg) translate(#{$circle-size *2},#{$circle-size + 10%}) rotate($rot * -1deg); } }
            @else if $i == 6 {
                &:nth-child(#{$i}) {
                    transform: rotate($rot * 1deg) translate(#{$circle-size *2},#{$circle-size - 200%}) rotate($rot * -1deg); } }
            @else if $i == 7 {
                &:nth-child(#{$i}) {
                    transform: rotate($rot * 1deg) translate(#{$circle-size * 1.3},#{$circle-size - 300%}) rotate($rot * -1deg); } }
            @else {
                &:nth-child(#{$i}) {
                    transform: rotate($rot * 1deg) translate(#{$circle-size*2}) rotate($rot * -1deg); } }


            $rot: $rot + $angle; } } }


